// Dfinit l'tat initial pour le reducer des employs.
const initialState = {
    employees: [], // Initialisation du tableau des employs.
};

/**
 * Reducer pour grer les actions lies aux employs.
 *
 * @param {Object} state - L'tat actuel de la partie des employs dans le store Redux.
 * @param {Object} action - L'action dispatche.
 * @returns {Object} - Le nouvel tat aprs application de l'action.
 */
function employeesReducer(state = initialState, action) {
    // Gre les actions en fonction de leur type.
    switch (action.type) {
        case 'ADD_EMPLOYEE':
            // Ajoute un nouvel employ  la liste des employs.
            return {
                ...state, // Copie l'tat actuel.
                employees: [...state.employees, action.payload], // Ajoute le nouvel employ  la liste.
            };
        default:
            // Retourne l'tat actuel si aucune action correspondante n'est trouve.
            return state;
    }
}

export default employeesReducer;